.order {
  display: grid;
  grid-template-areas:
  "title title"
  "list list"
  "text picture"
  "buttons picture";
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;

  @media screen and (max-width: 1200px) {
    column-gap: 50px;
  }

  @media screen and (max-width: 992px) {
    grid-template-columns: 1fr;
    grid-template-areas:
    "title"
    "list"
    "picture"
    "text"
    "buttons";
    column-gap: 0;
  }
}
